@import url(https://use.fontawesome.com/releases/v5.5.0/css/all.css);
@import url(https://use.fontawesome.com/releases/v5.5.0/css/all.css);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
}

html body {
  background-color: #f3f7f7
}
body::-webkit-scrollbar {
  display: none;
}
.wrapper{
  width: 100%;
  height: 100vh;
  display: flex;
  background-image: url(/static/media/login.65e27f3d.jpg);
  background-size: cover;
}
.login-box {
  
  width: 350px;
  height: 450px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #020005;
 
  border-radius: 12px;
  background-color: #ffffffc2;
  box-shadow: 2px 3px 9px #b977a2c4;
  font-family: 'Times New Roman', Times, serif;
}
.login-box h1 {
  font-size: 35px;
  margin-bottom: 25px;
  padding: 13px 0;
  color: #100d18;
  font-family: 'Courier New', Courier, monospace;

}
.head {
  margin-top: -35px;
}
.textbox {
  width: 100%;
  overflow: hidden;
  font-size: 20px;
  padding: 8px 0;
  margin: 8px 0;
}
.textbox i {
  margin-top: 10px;
  margin-left: 12px;
  width: 29px;
  float: left;
  text-align: center;
}
.textbox input {
  border: none;
  outline: none;
  background: transparent;
  color: #0a0909;
  font-size: 18px;
  font-weight: 400;
  width: 80%;
  float: left;
  margin: 0 10px;
  border-bottom: 1px solid #222;
}
.btns {
  width: 100%;
  /* background: none;  */
  border: 1px solid #ffffff9d;
  border-radius:20px ;
  color: #ffffff;
  font-weight: 900;
  padding: 5px;
  font-size: 20px;
  cursor: pointer;
  margin: 15px 2px;
}
.links{
  text-decoration: none;
  margin-bottom: 12px;

}
.errorMessage {
  font-size: 12px;
  letter-spacing: 0.05rem;
  color: #860a0a;
  padding-left: 0.25rem;
  font-style: italic;
}


.not-found {
  width: 560px;
  height: 225px;
  margin-right: -10px;
}
.starry-sky {
  display: block;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}
.search-icon {
  display: inline-block;
}
.notfound-copy {
  color: rgb(49, 12, 184);
  position: fixed;
  top: 25px;
  right: 10%;
  text-align: right;
}
h1 {
  font-weight: 700;
  font-size: 40px;
}
a {
  font-weight: 300;
  color: rgb(49, 12, 184);
  border-bottom: 1.5px solid rgb(49, 12, 184);
  text-decoration: none;
}
a:hover {
  font-weight: 300;
  color: rgb(49, 12, 184);
  border-bottom: 2px solid #000;
  text-decoration: none;
}
/* change to alternating star opacities */
.all-stars {
  animation: blinkblink 7s linear infinite;
}

@keyframes blinkblink {
  50% {
    opacity: 0.1;
  }
}

@media (max-width: 647px) {
  .moon {
    padding-top: -500px;
  }
}

